'use strict'

function debounce (fn, bufferInterval, ...args) {
  let timeout
  return () => {
    clearTimeout(timeout)
    timeout = setTimeout(fn.apply.bind(fn, this, args), bufferInterval)
  }
}

module.exports = debounce



